body{
  margin: 0;
  padding: 0;
  font-family: "montserrat",sans-serif;
  min-height: 100vh;
 
}
.notfound > h2 {
  margin-top: 10%;
  font-size: 50px;
  text-align: center; 
}
.notfound > h1{
